import Vue from "vue";
import Router from "vue-router";
import NProgress from "nprogress";
import ProfileUpdate from "./views/ProfileUpdate";
import SurveyPageOne from "./views/SurveyPageOne";
import SurveyPageTwo from "./views/SurveyPageTwo";
import SurveyPageThree from "./views/SurveyPageThree";
// import SurveyPageFour from "./views/SurveyPageFour";
import SurveyFinish from "./views/SurveyFinish";
import NotFound from "./views/NotFound";

import ProfileService from "@/services/ProfileService";
Vue.use(Router);

const router = new Router({
  // mode: "history",
  base: "/community/",
  routes: [
    {
      path: "/update-profile/:id",
      name: "update-profile",
      component: ProfileUpdate,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        ProfileService.getProfile(routeTo.params.id)
          .then(response => {
            routeTo.params.personInit = response.data.person;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/people/:id/update/1",
      name: "survey-one",
      component: SurveyPageOne,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        ProfileService.getProfile(routeTo.params.id)
          .then(response => {
            routeTo.params.person = response.data.person;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/people/:id/update/2",
      name: "survey-two",
      component: SurveyPageTwo,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        ProfileService.getProfile(routeTo.params.id)
          .then(response => {
            routeTo.params.person = response.data.person;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/people/:id/update/3",
      name: "survey-three",
      component: SurveyPageThree,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        ProfileService.getProfile(routeTo.params.id)
          .then(response => {
            routeTo.params.person = response.data.person;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    // {
    //   path: "/people/:id/update/4",
    //   name: "survey-four",
    //   component: SurveyPageFour,
    //   props: true,
    //   beforeEnter(routeTo, routeFrom, next) {
    //     ProfileService.getProfile(routeTo.params.id)
    //       .then(response => {
    //         routeTo.params.person = response.data.person;
    //         next();
    //       })
    //       .catch(() => {
    //         next({ name: "404" });
    //       });
    //   }
    // },
    {
      path: "/people/:id/update/finish",
      name: "survey-finish",
      component: SurveyFinish,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        ProfileService.getProfile(routeTo.params.id)
          .then(response => {
            routeTo.params.person = response.data.person;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/people/:id/surveys/:surveyId",
      name: "survey-one",
      component: SurveyPageOne,
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        ProfileService.getSurvey(routeTo.params.id, routeTo.params.surveyId)
          .then(response => {
            routeTo.params.person = response.data.person;
            routeTo.params.survey = response.data.survey;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/404",
      name: "404",
      component: NotFound
    },
    {
      path: "*",
      redirect: { name: "404" }
    }
  ]
});

router.beforeEach((routeTo, routeFrom, next) => {
  NProgress.start();
  next();
});

router.afterEach(() => {
  NProgress.done();
});

export default router;
