import axios from "axios";

const baseURL =
  process.env.NODE_ENV === "production"
    ? "https://tedxauckland-profiles.herokuapp.com//api/v1"
    : "http://localhost:3000/api/v1";

const apiClient = axios.create({
  baseURL: baseURL,
  withCredentials: false,
  headers: {
    Accept: "application/json",
    "Content-Type": "application/json"
  }
});

export default {
  getProfile(id) {
    return apiClient.get("/people/" + id);
  },
  updateProfile(id, data) {
    return apiClient.patch("/people/" + id, data);
  },

  getSurvey(personId, surveyId) {
    return apiClient.get(`/people/${personId}/surveys/${surveyId}`);
  },

  submitApplication(data) {
    return apiClient.post("/ticket_applications/", data);
  },
  getApplication(id) {
    return apiClient.get("/ticket_applications/" + id);
  },
  getTicketOffer(id) {
    return apiClient.get("/ticket_offers/" + id);
  },
  acceptTicket(data) {
    return apiClient.post("/ticket_offer_acceptances/", data);
  },
  rejectTicket(data) {
    return apiClient.post("/ticket_offer_rejections/", data);
  },
  updatePersonInfo(id, data) {
    return apiClient.patch("/ticket_offers/" + id, data);
  }
};
